<template>
    <div class="comment">
        <h3>评论</h3>
        <div class="comment-container">
            <div class="head">
                <div class="img">
                    <img :src="user.avatar || '../assets/logo.png'" />
                </div>
                <el-input
                    placeholder="请输入内容"
                    @change="handleAddComment"
                    v-model="value"
                    clearable
                >
                    <el-button
                        @click.stop="handleAddComment"
                        slot="append"
                        icon="el-icon-edit"
                        >评论</el-button
                    >
                </el-input>
            </div>
            <transition-group tag="ul" name="fade">
                <li
                    class="comment-item"
                    v-for="item in comments"
                    :key="item._id"
                >
                    <img
                        :src="
                            baseIMGURL + item.userid + '?byuserid=1' ||
                                '../assets/logo.png'
                        "
                    />
                    <div class="text-form">
                        <span class="user-name">
                            {{ item.username }}&ensp;:&ensp;
                        </span>
                        <span class="text">{{ item.context }}</span>
                        <span class="date"
                            >{{ item.date | fromNow }}
                            <span
                                class="del_comment"
                                @click.stop="handleDelComment(item._id)"
                                v-show="item.userid === user._id"
                                ><i class="el-icon-delete"></i></span
                        ></span>
                    </div>
                </li>
            </transition-group>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        comments: {
            type: Array,
            default() {
                return [];
            }
        }
    },
    data() {
        return {
            value: ""
        };
    },
    methods: {
        handleAddComment() {
            let value = this.value.trim();
            if (value.length < 1) return;
            this.$emit("handleAddComment", value);
        },
        handleDelComment(id) {
            this.$emit("handleDelComment", id);
        }
    }
};
</script>

<style lang="scss" scoped>
.comment {
    & > h3 {
        padding: 12px 0;
        font-size: 16px;
        font-weight: bold;
        border-bottom: 1px solid #f5f6f7;
    }
    ul {
        list-style: none;
    }
    .comment-container {
        width: 100%;
        height: 100%;
        div.head {
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
            width: 100%;
            padding: 1rem 0;
            .img {
                width: 32px;
                margin-top: 4px;
                padding-right: 1rem;
                height: 32px;
                img {
                    border-radius: $uni-border-radius-circle;
                    vertical-align: middle;
                    width: 32px;
                    height: 32px;
                }
            }

            // el-input {
            //     el-button {
            //     }
            // }
        }
        li.comment-item {
            padding: 1rem 0;
            display: flex;
            border-bottom: 1px solid #f5f6f7;
            flex-direction: row;
            &:last-of-type {
                border-bottom: 0;
            }
            img {
                border-radius: $uni-border-radius-circle;
                vertical-align: middle;
                margin-right: 1rem;
                width: 32px;
                height: 32px;
            }
            .text-form {
                line-height: calc(16px * 1.33333333);
                span.user-name {
                    font-size: 14px;
                }

                span.text {
                    margin: 0 0.5rem;
                    font-size: 16px;
                }
                span.date {
                    font-size: 14px;
                    color: $uni-text-color-grey;
                }
                span.del_comment {
                    padding: 0 1rem;
                    cursor: pointer;
                    color: $uni-color-primary;
                    &:hover {
                        color: $uni-color-error;
                    }
                }
            }
        }
    }
}
</style>