<template>
    <div class="info">
        <h1 class="title">
            个人信息
            <span class="user-setting" @click.stop="dialogFormVisible = true"
                >账号设置</span
            >
        </h1>
        <div class="info-detail" v-if="user._id">
            <div class="img">
                <img :src="user.avatar" />
                <p @click.stop="chooseIMG">修改头像</p>
            </div>
            <div class="info-container">
                <ul class="detail-container">
                    <li v-for="(value, key) in user" :key="key">
                        <p>
                            <span class="first">{{ key | user }}</span> :
                            <span
                                class="last"
                                v-if="typeof value === 'number'"
                                >{{ value | date1 }}</span
                            >
                            <span v-else>
                                {{ value }}
                            </span>
                        </p>
                    </li>
                </ul>
            </div>
        </div>
        <!-- 修改头像 -->
        <input
            type="file"
            @change="changeAvatar"
            ref="upload"
            accept="image/*"
            class="upload"
            name="avatar"
        />
        <!-- 修改账户信息 -->
        <el-dialog title="账户设置" :visible.sync="dialogFormVisible">
            <!-- 内层 -->
            <el-dialog
                width="30%"
                :title="changeType === 'psw' ? '修改密码' : '修改用户名'"
                :visible.sync="innerVisible"
                append-to-body
            >
                <template v-if="changeType === 'psw'">
                    <el-form>
                        <el-form-item
                            label="旧密码:"
                            :label-width="formLabelWidth"
                        >
                            <el-input
                                placeholder="请输入内容"
                                v-model.trim="form.prevpassword"
                                type="password"
                                clearable
                            >
                            </el-input>
                        </el-form-item>
                        <el-form-item
                            label="新密码:"
                            :label-width="formLabelWidth"
                        >
                            <el-input
                                placeholder="请输入内容"
                                type="password"
                                v-model.trim="form.newpasswprd"
                                clearable
                            >
                            </el-input>
                        </el-form-item>
                    </el-form>
                </template>
                <template v-if="changeType === 'name'">
                    <el-input
                        placeholder="请输入内容"
                        v-model.trim="form.name"
                        clearable
                    >
                    </el-input>
                </template>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="innerVisible = false">取 消</el-button>
                    <el-button type="primary" @click.stop="handleChangeSubmit">
                        确 定
                    </el-button>
                </div>
            </el-dialog>
            <!-- 外层 -->
            <el-form>
                <el-form-item label="账户密码:" :label-width="formLabelWidth">
                    <el-button
                        type="primary"
                        size="mini"
                        plain
                        icon="el-icon-edit"
                        @click.stop="handleChange('psw')"
                        >修改密码</el-button
                    >
                </el-form-item>
                <el-form-item label="用户名:" :label-width="formLabelWidth">
                    {{ user.name }}
                    <i
                        class="el-icon-edit edit"
                        @click.stop="handleChange('name')"
                    ></i>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.stop="dialogFormVisible = false"
                    >取 消</el-button
                >
                <el-button type="primary" @click="dialogFormVisible = false">
                    确 定
                </el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
export default {
    data() {
        return {
            dialogFormVisible: false,
            innerVisible: false,
            form: {
                name: "",
                prevpassword: "",
                newpasswprd: ""
            },
            changeType: ""
        };
    },
    methods: {
        changeAvatar(e) {
            let file = e.target.files[0];
            if (file) {
                let formData = new FormData();
                formData.append("file", file);
                this.$store.dispatch("changeAvatar", formData);
            }
        },
        chooseIMG() {
            this.$refs.upload.click();
        },
        handleChange(type) {
            this.changeType = type;
            this.innerVisible = !this.innerVisible;
        },
        handleChangeSubmit() {
            const DOFORM = {
                async psw() {
                    let data = await this.$store.dispatch("changePassword", {
                        ...this.form
                    });

                    this.innerVisible = !this.innerVisible;
                    if (data) this.$router.push("/login");
                },
                async name() {
                    let data = await this.$store.dispatch("changeUser", {
                        ...this.form
                    });
                    this.innerVisible = !this.innerVisible;
                    this.$store.commit("SETUSER", data);
                }
            };
            DOFORM[this.changeType]
                ? DOFORM[this.changeType].call(this)
                : console.log("未定义操作!");
        }
    },
    mounted() {}
};
</script>

<style lang="scss" scoped>
div.info {
    width: 100%;
    height: 100%;
    padding: 0.5rem;
    box-sizing: border-box;
    position: relative;
    .upload {
        width: 0;
        height: 0;
        position: absolute;
        z-index: 0;
    }
    h1.title {
        font-weight: bold;
        font-size: 20px;
        height: 90px;
        line-height: 90px;
        border-bottom: 1px solid $uni-bg-color-grey-more;
        .user-setting {
            color: $uni-color-primary;
            font-size: 14px;
            float: right;
            cursor: pointer;
        }
    }

    div.info-detail {
        display: flex;
        flex-direction: row;
        margin-top: 1rem;
        flex-wrap: wrap;
        div.img {
            width: 100px;
            margin-right: 1rem;
            img {
                width: 100px;
                height: 100px;
                border-radius: $uni-border-radius-circle;
            }
            p {
                width: 100%;
                color: $uni-color-primary;
                text-align: center;
                font-size: 14px;
                margin-top: 8px;
                cursor: pointer;
                &:hover {
                    color: $uni-color-warning;
                }
            }
        }

        div.info-container {
            width: 100%;
            padding: 0.5rem;
            box-sizing: border-box;
            ul.detail-container {
                li {
                    font-size: 14px;
                    color: $uni-text-color;
                    line-height: 28px;
                    letter-spacing: 1px;
                    p {
                        span {
                            line-height: 22px;
                        }
                        // .first {
                        // }

                        // .last {
                        // }
                    }
                }
            }
        }
    }
    ol,
    ul {
        list-style: none;
    }
}
.edit {
    float: right;
    margin-top: 13px;
    margin-right: 5px;
    cursor: pointer;
}
</style>